<template>
  <div id="app" class="g-container">
    <div class="g-view-container">
      <transition :name="transitionName">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      transitionName: ''
    }
  },
  watch: {
    $route (to, from) {
      if (to.meta.index > from.meta.index) {
        this.transitionName = 'transition-left'
      } else {
        this.transitionName = 'transition-right'
      }
    }
  }
}
</script>

<style lang="scss">
  .transition-left{
    &-enter-active,
    &-leave-active{
      transition: all 300ms;
      position: absolute;
    }
    &-enter{
      opacity: 0;
      transform: translate3d(100%, 0, 0);
    }
    &-leave-active{
      opacity: 0;
      transform: translate3d(-100%, 0, 0);
    }
  }
  .transition-right{
    &-enter-active,
    &-leave-active{
      transition: all 300ms;
      position: absolute;
    }
    &-enter{
      opacity: 0;
      transform: translate3d(-100%, 0, 0);
    }
    &-leave-active{
      opacity: 0;
      transform: translate3d(100%, 0, 0);
    }
  }
</style>



